import store from '../store/store';

/**
* Saves the logged user's details
* @param {Object} userData
*/
export function setSession(user) {
	store.commit('setLoggedUser', user);
}

/**
* Resets the logged-in user
*/
export function clearSession() {
	setSession({});
}

/**
* Retrieves the logged in user
* @return {Object} user - Stored user
*/
export function getSessionUser() {
	return store.getters.getLoggedUser;
}

/**
* Checks if a user is logged in
* //returns true if a user is logged in
* @returns {Boolean} returns true or false
*/
export function isLoggedIn() {
	return store.getters.isLoggedIn;
}

/**
* Stores the route to redirect to after succesful login
* @param {String} path - the path to redirect
*/
export function setRedirectURL(path) {
	store.commit('setRedirectUrl', path);
}

/**
* Gets the stored redirect path and resets it to the homepage
* @returns {String} path - the saved redirect path
*/
export function getRedirectUrl() {
	const url = store.getters.getRedirectUrl || '/';
	store.commit('setRedirectUrl', '/');
	return url;
}

/**
* Auth guard allows authenticated users only.
* @param to - next route
* @param from - previous route
* @param next - callback to transfer control to the next middleware
*/
export function requireAuth(to, from, next) {
	if (isLoggedIn()) {
		next();
	} else {
		setRedirectURL('/');
		next({ name: 'login' });
	}
}

/**
* Auth guard that allows non-auhtenticated users only.
* @param to - the next route
* @param from - the previous route
* @param next - callback to transfer control to the next middleware
*/
export function noAuthOnly(to, from, next) {
	if (isLoggedIn()) {
		next({ path: '/' });
	} else {
		next();
	}
}
