import axios from 'axios';

/**
* Logs in a User
* @param {Object} userData - user's email and password
* @returns {Promise} response - the response object from api
*/
export function login(userData) {
	return axios.post('v1/login', userData);
} 

/**
* Logs in a User
* @param {Object} userData - user's email 
* @returns {Promise} response - promise of email password to be reset
*/
export function resetPassword(email) {
	return axios.post('/v1/password', email);
}
