import Vue from 'vue';
import axios from 'axios';
import VueSession from 'vue-session';
import swal from 'sweetalert2';
import moment from 'moment';
import jwtDecode from 'jwt-decode';

import App from './App.vue';
import router from './router';
import store from './store/store';
import { setSession, clearSession } from './utils/auth';




import Editor from '@tinymce/tinymce-vue';
import tinymce from 'tinymce/tinymce';
import 'tinymce/themes/silver/theme';

// Any plugins you want to use has to be imported
import 'tinymce/plugins/image';
import 'tinymce/plugins/paste';
import 'tinymce/plugins/media';
import 'tinymce/plugins/imagetools';
import 'tinymce/plugins/code';
import 'tinymce/plugins/link';
import 'tinymce/plugins/autolink';
import 'tinymce/plugins/wordcount';
import 'tinymce/plugins/print';
import 'tinymce/plugins/preview';
import 'tinymce/plugins/searchreplace';
import 'tinymce/plugins/visualblocks';
import 'tinymce/plugins/template';
import 'tinymce/plugins/hr';
import 'tinymce/plugins/charmap';
import 'tinymce/plugins/insertdatetime';
import 'tinymce/plugins/lists';
import 'tinymce/plugins/help';
import 'tinymce/plugins/spellchecker';
import 'tinymce/plugins/visualchars';
import 'tinymce/plugins/codesample';
import 'tinymce/plugins/anchor';
import 'tinymce/plugins/advlist';

Vue.component('editor', Editor)
const options = {
	persist: true
};

Vue.use(VueSession, options);

Vue.config.productionTip = false;

axios.defaults.baseURL = process.env.VUE_APP_API_URL;
window.moment = moment;
window.swal = swal;

new Vue({
	router,
	store,
	data() {
		return {
			userId: this.$session.get('id')
		};
	},
	created() {
		if (this.isLoggedIn) {
			try {
				const token = jwtDecode(this.$session.get('token'));
				const time = Date.now().valueOf() / 1000;
				if (token.exp < time) {
					this.endSession();
				} else {
					this.resumeSession();
				}
			} catch (error) {
				this.endSession();
				return error;
			}
		}
		return true;
	},
	computed: {
		user() {
			return store.getters.getLoggedUser;
		},
		isLoggedIn() {
			return store.getters.isLoggedIn;
		},
		loggedUser() {
			return store.getters.getLoggedUser;
		}
	},
	watch: {
		isLoggedIn(newValue) {
			if (newValue) {
				this.startSession(this.loggedUser);
			}
		}
	},
	methods: {
		startSession(userData) {
			const user = {
				fname: userData.fname,
				lname: userData.lname,
				id: userData.id,
				avatar: userData.avatar,
				admin: userData.admin,
				category: userData.acl ? userData.acl.key : null
			};
			this.$session.start();
			this.$session.set('fname', user.fname);
			this.$session.set('lname', user.lname);
			this.$session.set('id', user.id);
			this.$session.set('admin', user.admin);
			this.$session.set('token', userData.token);
			this.$session.set('avatar', user.avatar);
			this.$session.set('category', user.category);
			setSession(user);
			axios.defaults.headers.common.token = userData.token;
		},
		resumeSession() {
			const user = {
				fname: this.$session.get('fname'),
				lname: this.$session.get('lname'),
				id: this.$session.get('id'),
				avatar: this.$session.get('avatar'),
				category: this.$session.get('category'),
				admin: this.$session.get('admin')
			};
			const token = this.$session.get('token');
			axios.defaults.headers.common.token = token;
			store.commit('setLoggedUser', user);
		},
		endSession() {
			this.$session.clear();
			this.$session.destroy();
			clearSession();
			axios.defaults.headers.common.token = null;
			store.commit('logout');
			setTimeout(() => {
				this.$router.push('/login');
			}, 500);
		}
	},
	render: h => h(App)
}).$mount('#app');
