<script>
import { mapGetters } from 'vuex';

export default {
	name: 'Header',
	data() {
		return {
			query: ''
		};
	},
	computed: {
		...mapGetters({
			loggedUser: 'getLoggedUser'
		})
	},
	methods: {
		search() {
			this.$router.push({
				name: 'search-users',
				params: {
					query: this.query
				}
			});
			this.query = '';
		}
	}
};
</script>

