router.beforeEach((to, from, next) => {
	const user = store.getters.getLoggedUser;
	if (to.meta.adminAuth) {
		if (user.category === 'administrator') {
			next();
		} else if (user.category === 'learning-content-manager') {
			if (to.meta.lcmAuth) {
				next();
			} else {
				next({ name: 'classes' });
			}
		} else if (user.category === 'employer') {
			if (to.meta.employerAuth) {
				next();
			} else {
				next({ name: 'jobs' });
			}
		} else if (user.category === 'support') {
			if (to.meta.supportAuth) {
				next();
			} else {
				next({ name: 'users' });
			}
		} else if (user.category === 'employer') {
			next({ name: 'jobs' });
		} else if (user.category === 'learning-content-manager') {
			next({ name: 'classes' });
		} else {
			next({name: 'login'})
		}
	} else {
		next();
	}
});

export default router;
