/* eslint no-shadow: ["error", { "allow": ["state"] }]	*/
import Vue from 'vue';
import Vuex from 'vuex';
import VueSession from 'vue-session';

import { login, resetPassword } from '../../utils/api/auth';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';
import router from '../../router';

Vue.use(Vuex);
Vue.use(VueSession);

const state = {
	status: '',
	isLoggedIn: !!(eventHub.$session.exists() && eventHub.$session.get('token') && eventHub.$session.get('id')),
	loggedUser: {},
	loginError: null,
	redirectUrl: '',
	error: {}
};

const getters = {
	getLoggedUser: (state) => {
		return state.loggedUser;
	},
	getStatus: (state) => {
		return state.status;
	},
	isLoggedIn: (state) => {
		return state.isLoggedIn;
	},
	getError: (state) => {
		return state.loginError;
	},
	getRedirectUrl: (state) => {
		return state.redirectUrl;
	}
};

const mutations = {
	loginSuccess: (state, user) => {
		state.loggedUser = user;
		state.isLoggedIn = true;
		state.loginError = false;
	},
	logout: (state) => {
		state.loggedUser = {};
		state.isLoggedIn = false;
	},
	loginError: (state) => {
		state.loginError = true;
	},
	authRequest: (state) => {
		state.status = 'loading';
	},
	authError: (state) => {
		state.status = 'error';
	},
	authSuccess: (state) => {
		state.status = 'success';
	},
	setLoggedUser: (state, user) => {
		state.loggedUser = user;
	},
	setRedirectUrl: (state, path) => {
		state.redirectUrl = path;
	},
	updateLoggedUser: (state, data) => {
		Object.keys(data).forEach(key => eventHub.$session.set(key.toString(), data[key]));
		state.loggedUser = { ...state.loggedUser, ...data };
	}
};

const actions = {
	login: ({ commit }, userData) => {
		commit('authRequest');
		login(userData)
			.then((response) => {
				if (response.status === 200 && ('token' in response.data)) {
					const user = response.data.data;
					user.token = response.data.token;
					commit('authSuccess');
					commit('loginSuccess', user);
					if (user.admin === 0) {
						fail({
							title: 'Oops!',
							text: 'Unathorised User'
						});
						router.push('/logout');
					} else {
						success({
							title: 'Login Success! You\'d be redirected shortly'
						});
					}
					return true;
				}
				commit('authError');
				commit('loginError');
				return false;
			})
			.catch((error) => {
				commit('authError');
				commit('loginError');
				fail({
					title: 'Oops! Something went wrong',
					text: error.data
				});
				return error;
			});
		return true;
	},
	resetPassword: ({ commit }, email) => {
		commit('authRequest');
		resetPassword(email)
			.then((response) => {
				if (response.status === 200) {
					success({
						title: 'Password reset success!'
					});
					commit('authSuccess');
					return true;
				}
				commit('authError');
				return false;
			})
			.catch((error) => {
				commit('authError');
				if (error.message === 'Request failed with status code 404') {
					fail({
						title: 'Oops!',
						text: 'User with email does does not exist'
					});
					return;
				}
				fail({
					title: 'Oops! Something went wrong, Please try again'
				});
			});
		return true;
	}

};

export default {
	actions,
	state,
	getters,
	mutations
};
